const sara = () => {

    const modalBtn = $('.sara__button');
    const modalClose = $('.sara__modal-knopa');
    const modalOrder = $('.modal-order');
    const modalTitle = $('.sara__subtitle'); //    
    const modalBtnHelp = $('.help__btn-modal'); //    help
    
    modalBtn.on('click', function() {  //  
        modalOrder.show(500);
    });

    modalBtnHelp.on('click', function() {  //  
        modalOrder.show(500);
    });

    modalOrder.click(function(e) {
        if ($(e.target).closest('.form-modal').length == 0) { //        
            $(this).fadeOut();					
        }
    });

    modalClose.on('click', function() {  //      
        modalOrder.hide(500);
    });

    $('.form-field__input-number').focus( function() { //      
        modalTitle
        .text(`  `)
    })

    $('.form-field__input-email').focus( function() { //      
        modalTitle
        .text(`  e-mail`)
    })

    $('.form-field__input').blur( function() { //           
        modalTitle.text(' ');
    })

    //   
    
    $('.sara__form-input').submit(function (event) {
        event.preventDefault();
    
        $.ajax({
            url: 'https://jsonplaceholder.typicode.com/todos',
            type: 'POST',
            data: $(this).serialize(),
            success(data) {
                modalTitle.text('   ,   ' + data.id)
            }, 
            error() {
                modalTitle.text('    ,  !')
            } 
        })
        
    });


}

sara();
