const form = () => {
    const formSubtitle = $('.form__subtitle'); //    

    $('.form-field__input-num').focus( () => { //      
        formSubtitle
        .text(`  `)
    })

    $('.form-field__input-mail').focus( () => { //      
        formSubtitle
        .text(`  e-mail`)
    })

    $('.form__inp').blur( () => { //           
        formSubtitle.text(' ');
    })

    //   
    
    $('.form__form-input').submit(function (event) {
        event.preventDefault();
        
        $.ajax({
            url: 'https://jsonplaceholder.typicode.com/todos',
            type: 'POST',
            data: $(this).serialize(),
            success(data) {
                formSubtitle.text('   ,   ' + data.id)
            }, 
            error() {
                formSubtitle.text('    ,  !')
            } 
        })
        
    });


};

form();

